<template>
<div class="titles">
    <div v-if="titleInfo">
        <span :style="titleStyle">{{ titleInfo.name }}</span>
        <div class="title-tooltip">
            <h2>{{ titleInfo.name }}</h2>
            <div v-if="statSet">
                称号属性
                <stat-grid :statSet="statSet"/>
            </div>
            <div v-else-if="fixstatSet">
                英雄称号属性
                <stat-grid :statSet="fixstatSet"/>
            </div>
            <div class="no-stats" v-else>无属性</div>
        </div>
    </div>
</div>
</template>

<script>
import GameInfo from '@/new/api/GameInfo';
import ItemStat from "@/old/api/item/itemstat";
import StatGrid from "@/old/game/StatGrid";
export default {
    components: {
        StatGrid,
    },
    props: ["titleInfo"],
    data: function() {
        return {

        };
    },
    computed:{
        statSet() {
            let ret = ItemStat.joinStatSet(this.titleInfo.state,"value");
            if(Object.keys(ret).length ==0){
                return false;
            }
            return ret;
        },
        fixstatSet() {
            let ret = ItemStat.joinStatSet(this.titleInfo.fixState,"value");
            if(Object.keys(ret).length ==0){
                return false;
            }
            return ret;
        },
        titleStyle(){
            const fg = `color: ${GameInfo.hexToColor(this.titleInfo._TextInColor)}`;
            const bg = `text-shadow: 0px 0px 2px ${GameInfo.hexToColor(this.titleInfo._TextOutColor)}`;
            return `${fg};${bg};`;
        },
    },
}
</script>

<style lang="less">
@import "../../less/core.less";

.titles {
    position: relative;
    display: inline-block;

    .title-tooltip {
        position: absolute;
        bottom: 100%;
        .left(0);
        // transform: translateX(-50%);
        display: block;
        opacity: 0.0;
        .text-align(left);
        //min-width: 100px;
        background: @dv-c-background;
        @supports ((-webkit-backdrop-filter: blur(1px)) or (backdrop-filter: blur(1px))) {
            background: fade(@dv-c-background, 80%);
            backdrop-filter: blur(8px);
        }
        z-index: 10;
        pointer-events: none;
        padding: 10px 16px;
        border: 1px solid @dv-c-foreground;
        box-shadow: 0 0 20px fade(black, 80%);
        //max-width: 400px;
        //min-width: 400px;
        transition: opacity 0.125s ease-in;

        h2 {
            font-size: 18px;
            padding: 0;
            margin: 0 0 8px 0;
        }
        .no-stats {
            color: @dv-c-body;
            padding: 4px 8px;
            width: 130px;
            margin: 6px 0 0 6px;
            text-transform: uppercase;
            letter-spacing: 0.2em;
            text-align: center;
            background-color: fade(@dv-c-red, 40%);
            margin:0 auto;
        }
    }

    &:hover {
        .title-tooltip {
            display: block;
            opacity: 1.0;
        }
    }
}

</style>

