<template>
  <div class='wrapper'>
    <h1 class="divided">成就</h1>
    <go-back />
    <tabs mode="horizon" @clickTab="getTabInfo" defaultTabId="0">
            <tab v-for="(v,k) in category" :key="k" :title="v" :id="k">
              <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
              </div>
              <template v-if="tabcache[k]">
                <div class="stats">
                  <tabs mode="horizon" class="content-tab">
                        <tab v-for="(vv,kk) in tabcache[k]" :key="kk" :title="vv.name">
                            <table>
                                <thead>
                                  <th>成就</th>
                                  <th>说明</th>
                                  <th>成就点数</th>
                                  <th style="width:95px;">称号</th>
                                  <th>获得物品</th>
                                </thead>
                                <tr v-for="(l,i) in vv.list" :key="i" style="height:60px;">
                                  <td>{{l.name}}</td>
                                  <td>{{l.subTitle}}</td>
                                  <td>{{l.point}}</td>
                                  <td><title-popup :titleInfo="l.title"></title-popup></td>
                                  <td>
                                    <div class="Title">
                                      <div v-for="(m,mk) in l.mailItem" :key="mk" class="titleitem">
                                          <item-icon-tooltip
                                                  :itemId="m.item.id" 
                                                  :itemStub="m.item"
                                                  :count="m.count"
                                                  :noLink="noLink">
                                          </item-icon-tooltip>
                                      </div>
                                    </div>
                                  </td>
                                </tr>
                            </table>
                        </tab>
                  </tabs>
                </div>
              </template>
            </tab>
    </tabs>
    <div v-if="!loading">
      <h3>成就奖励</h3>
      <table class="labryinth-table">
          <thead>
              <th>阶段</th>
              <th>成就点数</th>
              <th>奖励</th>
          </thead>
              <tr v-for="(v,k) in reward" :key="k">
                  <td>{{v.RowId}}</td>
                  <td>{{v._NeedPoint}}</td>
                  <td><div class="Title">
                          <div v-for="(vv,kk) in v.item" :key="kk" class="titleitem">
                              <item-icon-tooltip
                                      :itemId="vv.item.id" 
                                      :itemStub="vv.item"
                                      :count="vv.count"
                                      :noLink="noLink">
                              </item-icon-tooltip>
                          </div>
                      </div>
                  </td>
              </tr>
      </table>
    </div>
    <back-to-top><span class="topButton"><i class="fa fa-arrow-up"></i></span></back-to-top>
  </div>
</template>

<script>
import Vue from 'vue';
import Tab from "@/components/Tab/Tab";
import Tabs from "@/components/Tab/Tabs";
import GameInfo from '../api/GameInfo';
import Loader from "@/components/util/Loader";
import GoBack from "@/components/util/GoBack.vue";
import StatGrid from "@/old/game/StatGrid";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import BackToTop from '@/components/util/BacktoTop';
import TitlePopup from '@/old/game/TitlePopup.vue';
Vue.component("title-popup", TitlePopup);
Vue.component("load-indicator", Loader);
Vue.component("back-to-top", BackToTop);
export default {
  components: {
    Tab,
    Tabs,
    GoBack,
    StatGrid,
    ItemIconTooltip,
  },
  data () {
    return {
      tabId:0,
      loading:true,
      tabcache:[],
      category:[
        '一般',
        '关卡',
        '竞技场',
        '巢穴',
        '特殊',
      ],
      noLink:false,
      reward:null,
    }
  },
  methods: {
        getTabInfo(tabId){
          if(!this.tabcache[tabId]){
            this.loading = true;
              GameInfo.MissionInfo(tabId,this.$store.state.regionCode,
                  (res) => {
                      this.tabcache[tabId] = res.tabs;
                      this.loading = false;
                      this.reward = res.reward;
                  },
                  (err) => {
                      this.error = err;
                      this.loading = false;
                  });
          }
      },
  },
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.stats {
    overflow-x: auto;
    overflow-y:hidden;
    max-width: 250px;
    @media only screen and (min-width: 450px) {
        max-width: 380px;
    }

    @media only screen and (min-width: 550px) {
        max-width: 500px;
    }

    @media only screen and (min-width:650px) {
        max-width: 400px;
    }

    @media only screen and (min-width:780px) {
        max-width: 500px;
    }

    @media only screen and (min-width:900px) {
        max-width: 644px;
    }

    @media only screen and (min-width:@min-desktop-wide-width) {
        max-width: 950px;
    }
}

</style>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
.topButton{
  width: 60px;
  height: 60px;
  padding: 10px 10px;
  font-size: 22px;
  border:1px solid @dv-c-idle;
}

.content-tab{
  background-color: fade(@dv-c-background, 40%);
}
</style>

